import { Users_API } from "../api/api";
import OnlineFriends from "../src2/Navigation/OnlineFriends/OnlineFriends";


const ONLINE_FRIENDS = 'ONLINE_FRIENDS';

let initialState = {
    online: [
        { id: 1, name: 'Andrzej Duda', photo: 'data:image/jpeg;base64,' },
        { id: 2, name: 'Donald Trump', photo: 'data:image/jpeg;base64,' },
        { id: 3, name: 'Syava Krasava', photo: 'data:image/jpeg;base64,' },
        { id: 4, name: 'Sanya Sotka', photo: 'data:image/jpeg;base64,' },
        { id: 5, name: 'Dimas Normas', photo: 'data:image/jpeg;base64,' },
        { id: 6, name: 'Jana Pijana', photo: 'data:image/jpeg;base64,' }
    ],
}

const navbarReducer = (state = initialState, action) => {
    switch (action.type) {
        case ONLINE_FRIENDS: {
            state.online = action.data;
        }
        default: {
            state.online = []
        }
    }

    return state;
}

const setOnlineFriends = (data) => ({ type: ONLINE_FRIENDS, data })

const onlineFriends = () => {
    return async (dispatch) => {
        let responce = await Users_API.Follow().bind(Users_API);
        if(responce.resultCode === 0){
        dispatch(setOnlineFriends(responce));
}
    }
}
export default navbarReducer;